var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("灭霸")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Thanos是由Marvel漫画公司出版的美国漫画书中出现的虚构的超级恶魔。由作家/艺术家Jim Starlin创作的角色首次出现在The Invincible Iron Man＃55（封面日期为 1973 年 2月）中。Thanos是漫威宇宙中最强大的恶棍之一，并与许多英雄发生冲突，包括复仇者联盟，银河守护者，神奇四侠和X战警。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        Thanos首次出现在The Invincible Iron Man＃55（1973年2月）中，其中由Jim Starlin撰写的一则故事由Mike Friedrich共同编写。这个问题的故事情节继续通过Marvel上尉＃25-33（双月刊：1973年3月 - 1974年1月），Marvel Feature＃12（1973年11月），Daredevil＃107（1974年1月）和Avengers＃125（ 1974年7月）。他回归了一个延伸的故事情节，跨越了奇怪的故事＃178-181（1975年2月 - 8月），术士＃9 - 11（1975年10月 - 1976年1月），Marvel Team Up＃55（1977年3月），以及1977年复仇者联盟和奇迹二合一年度报告（Thanos实际上直到术士＃9 结束才会出现）。他还参加了Logan的Run＃6（1977年6月）的一个简短的备份故事，并在Marvel Captain图画小说之死（1982年4月）中扮演了一个小角色。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        作家兼艺术家Jim Starlin最初在大学心理学课程中构思了泰坦的Thanos 。正如Starlin所描述：我上大学之间做美军服和漫画工作，有一个心理课，我想出了Thanos ......和Drax the Destroyer，但我不确定他是如何融入它的，只是愤怒管理大概。所以我来到Marvel和Roy Thomas问我是否想做一个钢铁侠问题。我觉得这可能是我唯一有机会做一个角色，而不是有信心我的职业生涯将持续超过几周。所以他们陷入了困境。Thanos是一个更瘦的角色，Roy建议加强他，所以他从他原来的草图中加强了很多......后来我喜欢加强他以至于他的体型继续增长。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        斯塔林承认这个角色的外表受杰克柯比的Darkseid的影响：柯比做了新神，我认为这是太棒了。那时他在DC身边。我想出了一些受到启发的东西。你认为Thanos的灵感来自Darkseid，但当我出现时并非如此。在我的第一张Thanos图纸中，如果他看起来像任何人，那就是Metron。我拥有所有这些不同的神和我想做的事情，它们成了Thanos和泰坦。罗伊看了看Metron式椅子上的那个人，然后说道：“把他弄好了！如果你要偷一个新神，至少要扯掉Darkseid，那个非常好的！”")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br'),_c('h2',[_vm._v("影视作品及游戏")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"})])}
